input.map-switch {
    display: none;
}

label.switch-triple-label {
    color: white;
    cursor: pointer;
}

.active-font {
    color: black !important;
}

.switch-wrapper-container {
    display: inline-block;
    height: 40px;
    width: 250px;
}

.switch-container {
    display: inline-block;
    vertical-align: middle;
    width: 250px;
    height: 40px;
    border-radius: 100px;
    background-color: #4d4d4d;
    position: absolute;
    box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.5);
}

.switch-triple {
    height: 34px;
    width: 80px;
    background-color: white;
    border-radius: 100px;
    position: absolute;
    left: 3px;
    top: 3px;
    transition: ease-in;
}

/*---------------- LEFT ------------------- */
.switch-triple.left-to-center {
    animation-name: leftToCenter;
    animation-duration: 0.5s;
}

.switch-triple.left-to-right {
    animation-name: leftToRight;
    animation-duration: 0.5s;
}

.switch-triple.left-position {
    left: 3px;
}

.left-label {
    position: absolute;
}

.map-switch-left+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

.map-switch-left:checked+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

@keyframes leftToCenter {
    from {
        left: 3px;
    }

    to {
        left: 85px;
    }
}

@keyframes leftToRight {
    from {
        left: 3px;
    }

    to {
        left: 167px;
    }
}

/* -------------------- center ------------------ */
.switch-triple.center-to-left {
    animation-name: centerToLeft;
    animation-duration: 0.5s;
}

.switch-triple.center-to-right {
    animation-name: centerToRight;
    animation-duration: 0.5s;
}

.switch-triple.center-position {
    left: 85px;
}

.center-label {
    position: absolute;
    left: 85px;
}

.map-switch-center+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

.map-switch-center:checked+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

@keyframes centerToLeft {
    from {
        left: 85px;
    }

    to {
        left: 3px;
    }
}

@keyframes centerToRight {
    from {
        left: 85px;
    }

    to {
        right: 1px;
    }
}

/* ------------------- RIGHT ------------------- */
.switch-triple.right-to-left {
    animation-name: rightToLeft;
    animation-duration: 0.5s;
}

.switch-triple.right-to-center {
    animation-name: rightToCenter;
    animation-duration: 0.5s;
}

.switch-triple.right-position {
    left: 167px;
}

.right-label {
    position: absolute;
    right: 2px;
}

.map-switch-right+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

.map-switch-right:checked+label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 80px;
    border-radius: 100px;
}

@keyframes rightToLeft {
    from {
        left: 167px;
    }

    to {
        left: 3px;
    }
}

@keyframes rightToCenter {
    from {
        left: 167px;
    }

    to {
        right: 85px;
    }
}