.base-form {
    display: flex;
}


.multi-line-form-row {
    display: flex;
}

.multi-line-form-row-actions {
    margin-left: 0.5rem;
    display: flex;
    align-items: center; /* Vertical alignment */
    justify-content: center; /* Horizontal alignment, optional */
}

.multi-line-form-row-actions > button {
    border-radius: 1rem;
}