.cell-component {
    height: 2.0rem;
    width: 6.5rem;
    padding: 0.25rem 0.25rem;
    overflow-x: hidden;
    overflow-y: hidden;
}

.cell-component.hover:hover {
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.26);
}


.border-double-right {
    border-right: 3px double #e3e6f0 !important;
}

.cell-center {
    justify-content: center;
    text-align: center;
}

.cell-vertical-center {
    display: flex;
    align-items: center;
}

.cell-vertical-bottom {
    display: flex;
    align-items: end;
}

.cell-header {
    height: 2.5rem;
}

.cell-value {
    width: 100%;
    height: 100%;
}