.btn-future-date.selected {
    color: #fff !important;
    background-color: #447DF7 !important;
    border-color: #447DF7 !important;
    opacity: 1 !important;
    filter: alpha(opacity=100) !important;
}

.btn-future-date.selected:hover {
    color: #A6A6A6 !important;
    background-color: transparent !important;
    border-color: #A6A6A6 !important;
}

.btn-future-date {
    color: #A6A6A6 !important;
    background-color: transparent !important;
    border: 1px solid #A6A6A6 !important;
    opacity: 1 !important;
    cursor: pointer !important;
    padding: 0.15rem 0.25rem !important;
    min-width: 1.75rem !important;
    max-width: 2.25rem !important;
    width: 12.5%;
    margin-top: 0.25rem !important;
    margin-right: 0.25rem !important;
    font-weight: 500 !important;
    line-height: 21px !important;
}

.btn-future-date:hover {
    background-color: #447DF7 !important;
    color: #fff !important;
    border: 1px solid #447DF7 !important;
}

.btn-future-date:focus, .btn-future-date:active {
    outline: 0 !important;
    box-shadow: 0 0 0 0.2rem rgb(69, 125, 247, 0.2) !important;
}