.flow-item-schedule {
    margin-top: 1rem;
}

.flow-schedule-group {
    margin-top: .5rem;
}

.flow-schedule-group-display {
    text-align: center;
    font-weight: bolder;
    font-size: x-large;
    display: inline-grid;
    align-items: center;
    border-radius: 1rem;
    border: 1px solid #4e73df;
    overflow-x: hidden;
    padding: .75rem .20rem!important;
    
}

.flow-schedule-group-display .label {
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    writing-mode: vertical-lr;
}

.flow-schedule-group-col {
    display: flex;
    align-items: center;
}

.expand-option {
    display: flex;
    align-items: center;
    float: right !important;
    cursor: pointer;
    font-size: larger;
    font-weight: bolder;
    line-height: 100%;
}