.grid-container { 
    display: block;
    width: inherit;
}

.abstract-row {
    display: flex;
}

.abstract-cell {
    border: none !important;
}

.grid-cell {
    display: flex;
    min-width: 5rem;
    border: .1rem solid #d1d3e2;
    border-radius: .35rem;
    margin: .25rem .1rem;
    padding: .15rem .5rem;    
}

.grid-col-label {
    width: 15%;
    justify-content: center!important;
    font-weight: bolder;
    border: none;
    border-radius: 0;
    /* border-bottom: 2px solid #4e73df; */
}

.grid-col-label-internal {
    min-width: 35%;
    text-align: center!important;
    border-bottom: 2px solid #4e73df;
}
.grid-row {
    display: flex;
}

.grid-row-label {
    width: 12.5%;
    font-weight: bolder;
    border: 2px solid #4e73df;
    align-content: center;
    display: inline-grid;
}

.grid-cell-value {
    justify-content: center!important;
}

.grid-cell-value:has( > .value-item-accessor.active) { 
    border: .25rem solid #d1d3e2;
 }

