.table-ordering {
    padding-left: .5rem;
    display: inline-block;
}

.table-ordering-inner {
    display: flex;
    flex-direction: column;
}

.table-ordering-item {
    cursor: pointer;
    color: lightgrey;
    line-height: 15px !important;
}

.table-ordering-item:hover {
    color: rgb(83, 83, 83);
}

.table-ordering-item.active {
    color: rgb(83, 83, 83);
}

.table-ordering-item.active:hover {
    color: lightgrey;
}

.fa-25x {
    font-size: 2.5em;
}