.market-zone-list-container {
    max-height: 500px;
    padding: 0.75rem;
    overflow-y: scroll;
    border: 1px solid #e3e6f0;
    border-radius: 0.45rem;
}

.market-zone-list {
    margin-top: .5rem;
}

.market-zone-mgmt {
    margin-top: 1rem;
}

.market-zone-addition-options {
    margin-top: 0.75rem;
}

.map-tooltip {
    position: absolute;
    margin: 8px;
    padding: 4px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    max-width: 300px;
    font-size: 10px;
    z-index: 9;
    pointer-events: none;
}


.switch-button {
    width: 280px !important;
    padding-right: 140px !important;
    font-weight: 600 !important;
}

.switch-button:before {
    width: 140px !important;
}

.switch-button-checkbox:checked+.switch-button-label:before {
    transform: translateX(140px) !important;
}
